body{
    margin:0;
    padding:0;
}
.grid1{
    width:25%;
    float:left;
    text-align: center;
    background:#c0c0c0;
    padding-top:30px;
    height:95vh;
}
.grid1 h1{
    margin-top:40vh;
    font-size: 50px;
    color:white;
}
.grid{
    float:left;
    padding:30px 10px 0px 10px;
    height:95vh;
    display:grid;
    grid-template-columns:repeat(20, 25px);
    justify-content: center;
    width:50%;
    grid-auto-rows: 30px;
    background:#c0c0c0;
}
.grid2{
    float:right;
    width:23%;
    padding-top: 30px;
    background:#c0c0c0;
    height:95vh;
}
button{
    height:30px;
    width:25px;
    border:none;
    border-bottom:0.5px ridge white;
    border-left: 0.5px ridge white;
    font-size: 30px;
    outline:none;
    position:fixed;
}
.head {
    background:#00ff00;
    z-index:1;
    box-shadow:0 0 5px 5px #00ff00; 
    border:1px ridge white;
}
.body{
background:cyan;
}
.no{
    background:#191919;
    z-index:0;
}
.food{
    background:red;
    border-radius: 40px;
    z-index:1;
    animation: glow 0.8s infinite alternate;
}

@keyframes glow {
  from {
    box-shadow: 0 0 10px 10px red;
  }
  to {
    box-shadow: 0 0 10px 5px red;
  }
}